    getColor_date(day) {
      // console.log(day);
      var today = new Date();
      // console.log(today);
      var date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();

      var new_date = date.split("-");
      // console.log(new_date[1]);
      // console.log(day.split("-"));
      if (new_date[1] < 10) {
        if (new_date[2] < 10) {
          var new_day =
            today.getFullYear() + "-0" + new_date[1] + "-0" + new_date[2];
          // console.log(new_day);
          if (day == new_day) {
            return "green";
          }
          return "#FFFF";
        } else if (new_date[2] > 9) {
          var new_days =
            today.getFullYear() + "-0" + new_date[1] + "-s" + new_date[2];
          // console.log(new_days);
          if (day == new_days) {
            return "green";
          }
          return "#FFFF";
        }
      } else if (new_date[1] > 9) {
        if (new_date[2] < 10) {
          var new_day_1 =
            today.getFullYear() + "-0" + new_date[1] + "-0" + new_date[2];
          // console.log(new_day_1);
          if (day == new_day_1) {
            return "green";
          }
          return "#FFFF";
        } else if (new_date[2] > 9) {
          var new_days_1 =
            today.getFullYear() + "-0" + new_date[1] + "-s" + new_date[2];
          // console.log(new_days_1);
          if (day == new_days_1) {
            return "green";
          }
          return "#FFFF";
        }
      }
    },
  },
