<template>
  <div class="text-center">
    <v-dialog v-model="dialog_error" width="500">
      <v-alert
        width="500"
        border="right"
        colored-border
        type="error"
        elevation="2"
      >
        ขออภัยค่ะ ดาวน์โหลดข้อมูลไม่สำเร็จ
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false"> CLOSE </v-btn>
        </v-card-actions>
      </v-alert>
    </v-dialog>
    <v-dialog v-model="dialog" max-width="550">
      <v-card>
         <v-system-bar
      class="secondary"
      height="50"
      dark
    >
        <v-card-title class="white--text">นำไฟล์ออก</v-card-title>
         </v-system-bar>
        <v-row class="ma-0">
          <v-col class="mt-6 ml-5" cols="">
            <p>ชื่อไฟล์ :</p>
          </v-col>
          <v-col cols="9" class="mt-4 mr-4">
            <v-text-field
              label="ชื่อไฟล์"
              v-model="file_name"
              outlined
              rounded
              dense
              ref="form"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row class="mx-8 mb-4 mt-n4">
          <v-chip-group v-model="amenities" column multiple>
            <v-chip
            color="success"
              v-for="name in name"
              :key="name"
              :value="name"
              filter
              outlined
              @click="select_header(name)"
            >
              {{ name.text }}
            </v-chip>
            <v-chip filter outlined @click="select_all()" color="success">ทั้งหมด</v-chip>
          </v-chip-group>
        </v-row>
        <v-card-actions class="mt-n3">
          <v-spacer></v-spacer>
          <v-btn
            text
            rounded
            @click="dialog = false"
            class="mb-3"
          >
            ยกเลิก
          </v-btn>
          <v-btn
            class="mb-3 mr-3 secondary"
            rounded
            dark
            @click="set_message(file_name)"
          >
            ดาวน์โหลด
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      amenities: [],
      header: [],
      header_value : [], 
      data_list: [],
      dialog: false,
      status_all_button: false,
      name: [
        {
          text: "One Email",
          value: "one_email",
        },
        {
          text: "One ID",
          value: "employee_code",
        },
        {
          text: "เวลาเข้าพื้นที่",
          value: "check_in",
        },
        {
          text: "เวลาออกพื้นที่",
          value: "check_out",
        },
        {
          text: "สถานะ covid tracking",
          value: "covid_tracking",
        },
        {
          text: "วันที่",
          value: "date",
        },
        {
          text: "สถานที่เช็คอิน",
          value: "checkin_area",
        },
        {
          text: "สถานที่เช็คเอาท์",
          value: "checkout_area",
        },
      ],
    };
  },
  props: {
    message: String,
  },
  watch: {
    status_all_button(newValue) {
      if (newValue === true) {
        this.name.forEach((name_button) => {
          // console.log(name_button.text);
          if (!this.header.includes(name_button.text)) {
            this.header.push(name_button.text);
            this.header_value.push(name_button.value)
            // console.log(this.header);
          }
          this.amenities.push(name_button);
        });
      } else if (newValue === false) {
        this.header = [];
        this.amenities = [];
        this.header_value = []
      }
    },
  },
  methods: {
    select_all() {
      this.status_all_button = !this.status_all_button;
    },
    select_header(head) {
      // console.log(head);
      if (!this.header.includes(head.text)) {
        this.header.push(head.text);
        this.header_value.push(head.value)
      } else if (this.header.includes(head.text)) {
        var head_index = this.header.indexOf(head.text);
        this.header.splice(head_index, 1);
        var head_value_index = this.header_value.indexOf(head.value)
        this.header_value.splice(head_value_index, 1)
      }
      // console.log(this.header);
      // console.log(this.header_value);
    },
    filter_data(item) {
      const filtered = Object.keys(item)
        .filter((key) => this.header_value.includes(key))
        .reduce((obj, key) => {
          obj[key] = item[key];
          return obj;
        }, {});
      return filtered;
    },
    openDialogExport() {
      this.dialog = true;
    },
    set_message(file_name) {
      this.message.forEach((items) => {
        this.data_list.push(Object.values(this.filter_data(items)));
      });
      // console.log(this.data_list);
      this.export_file(file_name, this.data_list);
      // console.log(file_name);
    },
    async export_file(file_name, data_list) {
      var body = { head: this.header, data: data_list };
      await axios
        .post(
          process.env.VUE_APP_API +
            "/api/v1/report/export" +
            "?file_name=" +
            file_name,
          body,
          {
            headers: {
              Authorization: "Bearer " + this.$store.state.token_user,
            },
          }
        )
        .then((respones) => {
          // console.log("result=",result)
          // console.log("respones=", respones);
          if (respones.statusText == "OK") {
            var file_Name = respones.data.file_name;
            this.file_Path = respones.data.file_path;
            var Name = file_Name.split(".xlsx");
            this.download_file(Name[0], this.file_Path);
            this.header = []
            this.data_list = []
            this.amenities = []
            this.header_value = []
            this.dialog = false;
          }
        })
        .catch((error) => {
          // this.spinning = false;
          // console.log(error.message);
          // this.loading = false;
          if (error.message === "Request failed with status code 401") {
            // console.log("error 401");
            this.dialog_error = true;
          }
        });
    },
    async download_file(FileName, FilePath) {
      location.replace(
        process.env.VUE_APP_API +
          "/api/v1/report/export?file_path=" +
          FilePath +
          "&file_name=" +
          FileName
      );
      this.$refs.form.reset();
    },
  },
};
</script>